import { cx, css } from "emotion";
import { Title } from "reactism/elements";
import ItemFeature from "./partials/itemFeature";

const Feature = () => {
  const baseStyle = css`
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: auto auto;
    div.title {
      padding: 5rem 0 1.5rem 0;
      justify-self: center;
      margin-left: -5.5rem;
    }
    div.feature {
      padding-bottom: 7rem;
    }

    @media (max-width: 700px) {
      padding: 0 2em;
      div.title {
        padding-top: 2rem;
      }
      div.feature {
        padding-bottom: 2rem;
      }
    }
  `;

  return (
    <div className={baseStyle}>
      <div className="title">
        <Title left={6.5}>Layanan</Title>
      </div>
      <div className="feature">
        <ItemFeature />
      </div>
    </div>
  );
};

export default Feature;
